.card-container{
display: flex;
flex-direction: column;
background-color: violet;
border: 5px;
padding: 25px;
cursor: pointer;
-moz-osx-font-smoothing  :grayscale;
backface-visibility: hidden;
transform: translateZ(0);
transition: transform 0.25s ease-out;
}

.card-container:hover{
	transform: scale(1.05);
}